import React from "react";
import { Link } from "gatsby";
import { dateFormat } from "../components/Excerpt";

const Article = ( {data} ) => {
	return (
	<article className="Article mb-L">
		<h1 className="mb-0">
			<Link to={`/${data.fields.slug}`}>
				{data.frontmatter.title}
			</Link>
		</h1>
		<p className="flex mt-0 mb-L">
			<time className="mr-S">{ dateFormat(`${data.frontmatter.date}`) }</time>
			{data.frontmatter.camera && <aside>{data.frontmatter.camera}</aside>}
		</p>
		{(data.frontmatter.category === "photos" && data.frontmatter.description) && <p>
			{data.frontmatter.description}
		</p>}
		<div dangerouslySetInnerHTML={{ __html: data.html }} />
	</article>
)}

export default Article
